.content-fir,.content-sec{display:inline-block;opacity:0}
.animatedTime,.animatedTime-2{-webkit-animation-fill-mode:both}
html, body {
  position: relative;
  height: 100%
}
body{
  background: #eee;
  font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}
.swiper-container{
  width: 100%;
  height: 100%;
}
.swiper-slide{
  font-size: 18px;
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
}
.outwarp{
  position: absolute;
  top: 0;
  left: 0;
  z-index: -2;
  height: 100%;
  width: 100%;
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
}
.backgroundwarp, .innerwarp{position:absolute;top:0;left:0;z-index:-1;height:100%;width:100%;background-position:top}
.audio,.backgroundwarp,.innerwarp{background-size:contain;background-repeat:no-repeat}
.innerwarp{opacity:0}
.audio,.pub-passdown{z-index:10;position:absolute}
.audio{right:20px;top:7%;width:30px;height:30px;background-image:url(../images/normalmusic.svg)}
.pub-passdown{left:50%}
.pub1,.pub2{position:absolute;background-color:#fff;width:14px;height:2px;border-radius:2px;box-shadow:1px -1px 1px #646464}
.pub-passdown .pub1{-webkit-transform:rotate(-50deg);transform:rotate(-50deg);left:-10px}
.pub-passdown .pub2{left:-2px;-webkit-transform:rotate(50deg);transform:rotate(50deg)}
.content-contain{color:rgba(255,255,255,.5);top:60%;width:100%;position:absolute;text-align:center}
.content-fir{font-size:14px}
.content-sec{font-size:22px}
.topImage{height:100px;margin:20% auto 0;display:block;opacity:0;-webkit-transform:scale(1.5,1.5);transform:scale(1.5,1.5)}
.swiper-slide-active .topImage-active{width:100%;opacity:1;-webkit-transform:scale(.8,.8)!important;transform:scale(.8,.8)!important;-webkit-transition-property:-webkit-transform opacity;transition-property:-webkit-transform opacity;transition-property:transform opacity;transition-property:transform opacity,-webkit-transform opacity;-webkit-transition-duration:2s,4s;transition-duration:2s,4s;-webkit-transition-timing-function:ease-in;transition-timing-function:ease-in;-webkit-transition-delay:0;transition-delay:0}

